import React, { useState } from "react";
import { Row, Col } from "react-bootstrap";
import { Link, Route, Switch } from "react-router-dom";
import Create from "./Create";
import Edit from "./Edit";
import Delete from "./Delete";

function Editor() {
  const [mouseOver1, setMouseOver1] = useState("false");
  const [mouseOver2, setMouseOver2] = useState("false");
  const [mouseOver3, setMouseOver3] = useState("false");

  return (
    <div className="cntr">
      <Row>
        <h2>Welcome Admin</h2>
        <p className="bodyheader">What would you like to do?</p>
        <Col lg={4} className="zeropads">
          <Link className="link" to="/editor/new">
            <div
              className="article"
              onMouseOver={() => {
                setMouseOver1(!mouseOver1);
              }}
              onMouseOut={() => {
                setMouseOver1(!mouseOver1);
              }}
              style={{ border: mouseOver1 ? "" : "10px double gold" }}
            >
              <div className="options">
                <h4>Create New Post</h4>
              </div>
            </div>
          </Link>
        </Col>
        <Col lg={4} className="zeropads">
          <Link className="link" to="/editor/edit">
            <div
              className="article"
              onMouseOver={() => {
                setMouseOver2(!mouseOver2);
              }}
              onMouseOut={() => {
                setMouseOver2(!mouseOver2);
              }}
              style={{ border: mouseOver2 ? "" : "10px double gold" }}
            >
              <div className="options">
                <h4>Edit Existing PostPost</h4>
              </div>
            </div>
          </Link>
        </Col>
        <Col lg={4} className="zeropads">
          <Link className="link" to="/editor/delete">
            <div
              className="article"
              onMouseOver={() => {
                setMouseOver3(!mouseOver3);
              }}
              onMouseOut={() => {
                setMouseOver3(!mouseOver3);
              }}
              style={{ border: mouseOver3 ? "" : "10px double gold" }}
            >
              <div className="options">
                <h4>Delete</h4>
              </div>
            </div>
          </Link>
        </Col>
      </Row>

      <Switch>
        <Route exact path="/editor/new" component={Create} />
        <Route exact path="/editor/edit" component={Edit} />
        <Route exact path="/editor/delete" component={Delete} />
      </Switch>
    </div>
  );
}

export default Editor;
